const ContentBase = props => {
  console.log("ContentBase Props", props);
  let datePublishedToDsiplay = "";
  if (props.pageContent.datePublished) {
//     if (props.pageContent.datePublished.getFullYear) {
//       datePublishedToDsiplay = `${props.pageContent.datePublished.getFullYear()}.${("00" + props.pageContent.datePublished.getMonth() + 1).slice(-2)}.${("00" + props.pageContent.datePublished.getDate()).slice(-2)}`;
//     }
    if (props.pageContent.datePublished.substr) {
      datePublishedToDsiplay = `${props.pageContent.datePublished.substr(0, 4)}.${("00" + props.pageContent.datePublished.substr(5, 2)).slice(-2)}.${("00" + props.pageContent.datePublished.substr(8, 2)).slice(-2)}`;
    }
  }

  let datePublishedToValue = "";
  if (props.pageContent.datePublished) {
//     if (props.pageContent.datePublished.getFullYear) {
//       datePublishedToValue = `${props.pageContent.datePublished.getFullYear()}-${("00" + props.pageContent.datePublished.getMonth() + 1).slice(-2)}-${("00" + props.pageContent.datePublished.getDate()).slice(-2)}T${("00" + props.pageContent.datePublished.getHours()).slice(-2)}:${("00" + props.pageContent.datePublished.getMinutes()).slice(-2)}+09:00`;
//     }
    datePublishedToValue = props.pageContent.datePublished;
  }

  return (
    <React.Fragment>
      <Head>
        <title>{ props.pageContent.title } &#x007c; { props.siteName }</title>
        <meta name="description" content={ props.pageContent.description } />
      </Head>
      <article className="p2 md-pt3 md-px3 bg-white">
        <header className="article-header mb3">
          <h1>{ props.pageContent.title }</h1>
          <div className="flex flex-wrap">
            <div>{/*<span className="pr1">投稿日:</span>*/}<time className="" dateTime={ datePublishedToValue }>{ datePublishedToDsiplay }</time></div>
          </div>
        </header>
        <div className="article-body" dangerouslySetInnerHTML={{ __html: props.pageContent.body }} />
      </article>
      <style jsx global>{`
        body {
          word-wrap: break-word;
        }
        .article-header h1 {
          font-size: 2rem;
          line-height: 1.25;
        }
        .article-body h2 {
          font-size: 1.5rem;
          line-height: 1.3333333333;
          margin-top: 3rem;
          padding-top: 1rem;
          padding-bottom: 1rem;
          border-top: solid 1px #000;
          border-bottom: solid 1px #000;
        }
        .article-body h3 {
          font-size: 1.25rem;
          line-height: 1.4;
          padding-top: .75rem;
        }
        .article-body h4 {
          font-size: 1rem;
          line-height: 1.5;
          padding-top: 1rem;
        }
        .article-body h5 {
          font-size: .875rem;
          line-height: 1.5714285714;
          padding-top: 1.125rem;
        }
        .article-body h6 {
          font-size: .75rem;
          line-height: 1.6666666667;
          padding-top: 1.25rem;
        }
        .article-body div.sourceCode {
          margin: 0;
          margin-left: -1rem;
          margin-right: -1rem;
          margin-bottom: 1rem;
          padding: 0;
        }
        .article-body pre.sourceCode {
          madgin: 0;
          padding: 1rem;
          overflow-x: auto;
          color: #333;
          background: #f8f8f8;
          border: 1px solid #ddd;
          border-radius: .25rem;
          font-size: .75rem;
          max-width: 100%;
        }
        .article-body code.sourceCode,
        pre > code {
          background-color: transparent;
          padding: 0;
        }
        @media all and (min-width: 50em) {
          .article-body div.sourceCode {
            margin-left: -2rem;
            margin-right: -2rem;
          }
          .article-body pre.sourceCode {
            padding-left: 2rem;
            padding-right: 2rem;
          }
        }
      `}</style>
    </React.Fragment>
  );
};


// 子コンポーネントでは使えないらしい ... ドキュメントに記載あり。
